.mainComponent{
    transition: background-color 1.3s ease; /* This will animate the background color change */
    border: 1px #ea8282 solid;
    width: 300px;
    height: 400px;
}

.intersecting {
    background-color: #baffc9; /* Light green background to indicate visibility */
}

.image{
    max-width: 100%;
    max-height: 50%;
}

.informationListing{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 15px 10px;
    gap: 10px;
}

.description{
    display: flex;
}

.previewText{
    font-size: 20px;
    font-family: sans-serif;
    font-weight: normal;

}

.previewIcon{
    width: 30px;
    height: 30px;
    margin-right: 5px;
}
